import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useApp } from '../context';
import { CURRENCIES, AppSettings } from '../types';

const settingsVariants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1,
      delayChildren: 0.1
    }
  }
};

const cardVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: { 
    opacity: 1, 
    y: 0,
    transition: { duration: 0.4 }
  }
};

export function SettingsPage() {
  const { state, updateSettings, exportData, importData, clearAllData } = useApp();
  const [isExporting, setIsExporting] = useState(false);
  const [isImporting, setIsImporting] = useState(false);
  const [isClearing, setIsClearing] = useState(false);

  const handleCurrencyChange = (currencyCode: string) => {
    const currency = CURRENCIES.find(c => c.code === currencyCode);
    if (currency) {
      const newSettings: AppSettings = {
        ...state.settings,
        currency
      };
      updateSettings(newSettings);
    }
  };

  const handleRetentionChange = (months: number) => {
    const newSettings: AppSettings = {
      ...state.settings,
      historyRetentionMonths: months
    };
    updateSettings(newSettings);
  };

  const handleExport = async () => {
    try {
      setIsExporting(true);
      const data = await exportData();
      
      // Create and download file
      const blob = new Blob([data], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `financial-tracker-backup-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
      
      alert('Data exported successfully!');
    } catch (error) {
      alert('Failed to export data');
    } finally {
      setIsExporting(false);
    }
  };

  const handleImport = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    try {
      setIsImporting(true);
      const text = await file.text();
      await importData(text);
      alert('Data imported successfully!');
    } catch (error) {
      alert('Failed to import data. Please check the file format.');
    } finally {
      setIsImporting(false);
      // Reset the input
      event.target.value = '';
    }
  };

  const handleClearData = async () => {
    const confirmed = window.confirm(
      'Are you sure you want to delete ALL data? This action cannot be undone.\n\nType "DELETE" in the next prompt to confirm.'
    );
    
    if (!confirmed) return;

    const confirmation = window.prompt(
      'Type "DELETE" to confirm you want to permanently delete all data:'
    );

    if (confirmation !== 'DELETE') {
      alert('Data deletion cancelled.');
      return;
    }

    try {
      setIsClearing(true);
      await clearAllData();
      alert('All data has been deleted.');
    } catch (error) {
      alert('Failed to clear data');
    } finally {
      setIsClearing(false);
    }
  };

  if (state.loading) {
    return <div className="loading">Loading...</div>;
  }

  return (
    <motion.div
      variants={settingsVariants}
      initial="hidden"
      animate="visible"
    >
      <motion.h1 
        className="mb-2 font-large font-bold"
        initial={{ opacity: 0, y: -30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        Settings
      </motion.h1>

      {/* Currency Settings */}
      <motion.div className="card" variants={cardVariants}>
        <h2 className="mb-1 font-bold">Currency</h2>
        <div className="form-group">
          <label className="form-label">Display Currency</label>
          <select
            className="select"
            value={state.settings.currency.code}
            onChange={(e) => handleCurrencyChange(e.target.value)}
          >
            {CURRENCIES.map(currency => (
              <option key={currency.code} value={currency.code}>
                {currency.symbol} {currency.name} ({currency.code})
              </option>
            ))}
          </select>
        </div>
      </motion.div>

      {/* History Settings */}
      <motion.div className="card" variants={cardVariants}>
        <h2 className="mb-1 font-bold">History</h2>
        <div className="form-group">
          <label className="form-label">Keep history for (months)</label>
          <select
            className="select"
            value={state.settings.historyRetentionMonths}
            onChange={(e) => handleRetentionChange(parseInt(e.target.value))}
          >
            <option value={3}>3 months</option>
            <option value={6}>6 months</option>
            <option value={12}>12 months</option>
            <option value={24}>24 months</option>
            <option value={36}>36 months</option>
          </select>
        </div>
        <p style={{ fontSize: '0.9rem', color: '#666', marginTop: '0.5rem' }}>
          Currently storing {state.budgets.length} month{state.budgets.length !== 1 ? 's' : ''} of data
        </p>
      </motion.div>

      {/* Data Management */}
      <motion.div className="card" variants={cardVariants}>
        <h2 className="mb-2 font-bold">Data Management</h2>
        
        {/* Export */}
        <div className="mb-2">
          <h3 className="mb-1 font-bold" style={{ fontSize: '1rem' }}>Export Data</h3>
          <p style={{ fontSize: '0.9rem', color: '#666', marginBottom: '1rem' }}>
            Download all your budget data as a JSON file for backup or transfer.
          </p>
          <motion.button
            className="w-full button"
            onClick={handleExport}
            disabled={isExporting}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            {isExporting ? 'Exporting...' : '📤 Export Data'}
          </motion.button>
        </div>

        {/* Import */}
        <div className="mb-2">
          <h3 className="mb-1 font-bold" style={{ fontSize: '1rem' }}>Import Data</h3>
          <p style={{ fontSize: '0.9rem', color: '#666', marginBottom: '1rem' }}>
            Import budget data from a JSON file. This will replace all current data.
          </p>
          <motion.label 
            className="w-full button" 
            style={{ display: 'block', textAlign: 'center', cursor: 'pointer' }}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            {isImporting ? 'Importing...' : '📥 Import Data'}
            <input
              type="file"
              accept=".json"
              onChange={handleImport}
              disabled={isImporting}
              style={{ display: 'none' }}
            />
          </motion.label>
        </div>

        {/* Clear Data */}
        <div>
          <h3 className="mb-1 font-bold" style={{ fontSize: '1rem' }}>Reset All Data</h3>
          <p style={{ fontSize: '0.9rem', color: '#666', marginBottom: '1rem' }}>
            Permanently delete all budget data. This action cannot be undone.
          </p>
          <motion.button
            className="w-full button button-danger"
            onClick={handleClearData}
            disabled={isClearing}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            {isClearing ? 'Clearing...' : '🗑️ Delete All Data'}
          </motion.button>
        </div>
      </motion.div>

      {/* App Info */}
      <motion.div className="card" variants={cardVariants}>
        <h2 className="mb-1 font-bold">About</h2>
        <div style={{ fontSize: '0.9rem', color: '#666' }}>
          <p><strong>Financial Tracker PWA</strong></p>
          <p>Version 1.0.0</p>
          <p className="mt-1">Built with React and IndexedDB for offline use</p>
          <p className="mt-1">✅ Works offline</p>
          <p>✅ Mobile optimized</p>
          <p>✅ Data stored locally</p>
        </div>
      </motion.div>

      {state.error && (
        <motion.div 
          className="error"
          variants={cardVariants}
        >
          {state.error}
        </motion.div>
      )}
    </motion.div>
  );
}
